import { useRef, useState } from 'react';
import classes from './Checkout.module.css';
import React, { Component }  from 'react';

const isEmpty = value => value.trim() === "";
const isFiveDigits = value => value.trim().length === 5;
const isTenDigits = value => value.trim().length === 10;

const Checkout = (props) => {
    const [formInputValid, setFormInputValid] = useState({
        name: true,
        address: true,
        city: true,
        zip: true,
        phone: true,
        email: true
    });

    const nameInputref = useRef();
    const addressInputref = useRef();
    const zipInputref = useRef();
    const cityInputref = useRef();
    const phoneInputref = useRef();
    const emailInputref = useRef();

    const confirmHandler = (event) => {
        event.preventDefault();

        const enteredName = nameInputref.current.value;
        const enteredAddress = addressInputref.current.value;
        const enteredZip = zipInputref.current.value;
        const enteredCity = cityInputref.current.value;
        const enteredPhone = phoneInputref.current.value;
        const enteredEmail = emailInputref.current.value;
    
        const enteredNameIsValid = !isEmpty(enteredName);
        const enteredAddressIsValid = !isEmpty(enteredAddress);
        const entereZipIsValid = !isEmpty(enteredZip) && isFiveDigits(enteredZip);
        const enteredCityIsValid = !isEmpty(enteredCity);
        const enteredPhoneIsValid = !isEmpty(enteredPhone) && isTenDigits(enteredPhone);
        const enteredEmailIsValid = !isEmpty(enteredEmail);

        setFormInputValid({
            name: enteredNameIsValid,
            address: enteredAddressIsValid,
            zip: entereZipIsValid,
            city: enteredCityIsValid,
            phone: enteredPhoneIsValid,
            email: enteredEmailIsValid
        })

        const formIsValid = enteredNameIsValid &&
                            enteredAddressIsValid &&
                            entereZipIsValid &&
                            enteredCityIsValid &&
                            enteredPhoneIsValid &&
                            enteredEmailIsValid
        
        if (!formIsValid) {
            return;
        }

        props.onConfirm({
            name: enteredName,
            address: enteredAddress,
            zip: enteredZip,
            city: enteredCity,
            phone: enteredPhone,
            email: enteredEmail

        })
    };

    const nameControlClasses = `${classes.control} ${
        formInputValid.name ? "" : classes.invalid
    }`
    const addressControlClasses = `${classes.control} ${
        formInputValid.address ? "" : classes.invalid
    }`
    const zipControlClasses = `${classes.control} ${
        formInputValid.zip ? "" : classes.invalid
    }`
    const cityControlClasses = `${classes.control} ${
        formInputValid.city ? "" : classes.invalid
    }`
    const phoneControlClasses = `${classes.control} ${
        formInputValid.phone ? "" : classes.invalid
    }`
    const emailControlClasses = `${classes.control} ${
        formInputValid.email ? "" : classes.invalid
    }`

    return (
    <form className={classes.form} onSubmit={confirmHandler}>
        
        <div className={nameControlClasses}>
            <label htmlFor='name'>Name</label>
            <input type='text' id='name' ref={nameInputref}/>
            {!formInputValid.name && <p>Please enter a valid name.</p>}
        </div>
        
        <div className={addressControlClasses}>
            <label htmlFor='address'>Address</label>
            <input type='text' id='address' ref={addressInputref}/>
            {!formInputValid.address && <p>Please enter a valid address.</p>}
        </div>

        <div className={zipControlClasses}>
            <label htmlFor='zip'>Zip</label>
            <input type='text' id='zip' ref={zipInputref}/>
            {!formInputValid.zip && <p>Please enter a valid zip (5 digits).</p>}
        </div>
        
        <div className={cityControlClasses}>
            <label htmlFor='city'>City</label>
            <input type='text' id='city' ref={cityInputref}/>
            {!formInputValid.city && <p>Please enter a valid city.</p>}
        </div>

        <div className={phoneControlClasses}>
            <label htmlFor='phone'>Phone</label>
            <input type='text' id='phone' ref={phoneInputref}/>
            {!formInputValid.phone && <p>Please enter a valid phone number (10 digits).</p>}
        </div>

        <div className={emailControlClasses}>
            <label htmlFor='email'>Email</label>
            <input type='email' id='email' ref={emailInputref}/>
            {!formInputValid.email && <p>Please enter a valid email.</p>}
    </div>
    
        <div className={classes.actions}>
            <button type='button' onClick={props.onCancel}>
            Cancel
            </button>
            <button className={classes.submit}>Confirm</button>
        </div>
    </form>
    );
};

export default Checkout;